import { useState } from 'react';

import './style.css'

function App() {
  return (
    <div>
      <main>
        <Calculator />
      </main>
    </div>
  );
}

function Formula(props) {
  return (
    <div className='formula' id="formula">{props.theFormula}</div>
  );
}

function Output(props) {
  return (
    <div id='display' className='output'>
      {props.theOutput}
    </div>
  );
}

function Buttons(props) {
  return (
    <div>
      <div className="buttonContainer">
        <button id="clear" className="button-style sign-button-color two-grid-column" value="AC" onClick={props.handlerFunction}>AC</button>
        <button id="divide" className="button-style sign-button-color " value="/" onClick={props.handlerFunction}>/</button>
        <button id="multiply" className="button-style sign-button-color " value="*" onClick={props.handlerFunction}>*</button>
        <button id="seven" className="button-style  " value="7" onClick={props.handlerFunction}>7</button>
        <button id="eight" className="button-style  " value="8" onClick={props.handlerFunction}>8</button>
        <button id="nine" className="button-style  " value="9" onClick={props.handlerFunction}>9</button>
        <button id="add" className="button-style sign-button-color " value="+" onClick={props.handlerFunction}>+</button>
        <button id="four" className="button-style  " value="4" onClick={props.handlerFunction}>4</button>
        <button id="five" className="button-style  " value="5" onClick={props.handlerFunction}>5</button>
        <button id="six" className="button-style  " value="6" onClick={props.handlerFunction}>6</button>
        <button id="subtract" className="button-style sign-button-color " value="-" onClick={props.handlerFunction}>-</button>
        <button id="one" className="button-style  " value="1" onClick={props.handlerFunction}>1</button>
        <button id="two" className="button-style  " value="2" onClick={props.handlerFunction}>2</button>
        <button id="three" className="button-style  " value="3" onClick={props.handlerFunction}>3</button>
        <button id="equals" className="button-style sign-button-color two-grid-row" value="=" onClick={props.handlerFunction}>=</button>
        <button id="zero" className="button-style  two-grid-column" value="0" onClick={props.handlerFunction}>0</button>
        <button id="decimal" className="button-style  " value="." onClick={props.handlerFunction}>.</button>
      </div>
    </div>
  );
}

function Calculator() {

  const [selector, setSelector] = useState("0");
  const [formula, setFormula] = useState("");

  const doubleOperatorChecker = /(\/|\+|-|\*)-$/;
  const operatorChecker = /(\/|\+|-|\*)$/;
  
  function handleClick(e) {
    const formulaDiv = document.getElementById("formula");
    const selectorDiv = document.getElementById("display");
    if (e.target.value === 'AC') {
      clearHandle(formulaDiv, selectorDiv);
    }
    else if (/\d/.test(e.target.value)) {
      numberHandle(e, formulaDiv, selectorDiv);
    }
    else if (/\./.test(e.target.value) && !/\./.test(selectorDiv.innerText)) {
      decimalHandle(e, formulaDiv, selectorDiv);
    }
    else if (/\/|\+|-|\*/.test(e.target.value)) {
      operatorHandle(e, formulaDiv, selectorDiv);
    }
    else if (e.target.value === '=') {
      evulateHandle(e, formulaDiv, selectorDiv);
    }
  }

  function clearHandle(formulaDiv, selectorDiv) {
    formulaDiv.innerText = "";
    selectorDiv.innerText = "0";
    setSelector("0");
    setFormula("");
  }

  function numberHandle(e, formulaDiv, selectorDiv) {
    let prsFloat = parseFloat(selector + e.target.value);
    if (isNaN(prsFloat)) {
      formulaDiv.innerText = formulaDiv.innerText + e.target.value;
      selectorDiv.innerText = e.target.value;
      setFormula(formulaDiv.innerText);
      setSelector(e.target.value);
    }
    else {
      if (selectorDiv.innerText === '0') {
        selectorDiv.innerText = e.target.value;
        formulaDiv.innerText = formulaDiv.innerText + e.target.value
        setSelector(parseFloat(selectorDiv.innerText));
        setFormula(formulaDiv.innerText);
      }
      else {
        selectorDiv.innerText = selectorDiv.innerText + e.target.value;
        formulaDiv.innerText = formulaDiv.innerText + e.target.value;
        setSelector(selectorDiv.innerText);
        setFormula(formulaDiv.innerText);
      }
    }
  }

  function decimalHandle(e, formulaDiv, selectorDiv) {
    if (formulaDiv.innerText === '' && selectorDiv.innerText === '0') {
      selectorDiv.innerText = selectorDiv.innerText + e.target.value;
      formulaDiv.innerText = selectorDiv.innerText;
      setSelector(selectorDiv.innerText);
      setFormula(formulaDiv.innerText);
      return;
    }
    if (operatorChecker.test(formulaDiv.innerText)) {
      return;
    }
    selectorDiv.innerText = selectorDiv.innerText + e.target.value;
    formulaDiv.innerText = formulaDiv.innerText + e.target.value;
    setSelector(selectorDiv.innerText);
    setFormula(formulaDiv.innerText);
  }

  function operatorHandle(e, formulaDiv, selectorDiv) {
    if (formulaDiv.innerText === '' && selectorDiv.innerText === '0' && e.target.value !== '-') {
      return;
    }
    if (doubleOperatorChecker.test(formulaDiv.innerText)) {
      selectorDiv.innerText = e.target.value;
      formulaDiv.innerText = formulaDiv.innerText.replace(doubleOperatorChecker, e.target.value);
      setSelector(selectorDiv.innerText);
      setFormula(formulaDiv.innerText);
      return;
    }
    if (operatorChecker.test(formulaDiv.innerText)) {
      if (e.target.value === '-') {
        selectorDiv.innerText = e.target.value;
        formulaDiv.innerText = formulaDiv.innerText + e.target.value;
        setSelector(selectorDiv.innerText);
        setFormula(formulaDiv.innerText);
        return;
      }
      selectorDiv.innerText = e.target.value;
      formulaDiv.innerText = formulaDiv.innerText.replace(operatorChecker, e.target.value); 
      setSelector(selectorDiv.innerText);
      setFormula(formulaDiv.innerText);
      return;
    }
    selectorDiv.innerText = e.target.value;
    formulaDiv.innerText = formulaDiv.innerText + e.target.value;
    setSelector(selectorDiv.innerText);
    setFormula(formulaDiv.innerText);
    return;
  }

  function evulateHandle(e, formulaDiv, selectorDiv) {
    selectorDiv.innerText = Math.round(1000000000000 * eval(formulaDiv.innerText)) / 1000000000000;
    formulaDiv.innerText = selectorDiv.innerText;
    setSelector(selectorDiv.innerText);
    setFormula(formulaDiv.innerText);
  }

  return (
    <div className='container'>
      <Formula theFormula={formula} />
      <Output theOutput={selector} />
      <Buttons handlerFunction={handleClick} />
    </div>
  );
}

export default App;
